package io.github.mee1080.umasim.scenario.onsen

import io.github.mee1080.umasim.data.Status
import io.github.mee1080.umasim.scenario.CommonScenarioEvents
import io.github.mee1080.umasim.simulation2.*
import kotlin.math.max
import kotlin.math.min

class OnsenScenarioEvents : CommonScenarioEvents() {

    override suspend fun beforeAction(state: SimulationState, selector: ActionSelector): SimulationState {
        val base = super.beforeAction(state, selector)
        val onsenStatus = state.onsenStatus ?: return base
        return when (base.turn) {
            3, 25, 49, 66 -> base.selectGensen(selector)
            37, 61 -> base.addOnsenTicket(onsenTicketOnDig[onsenStatus.hoshinaRarity])
            else -> base
        }
    }

    override suspend fun afterAction(state: SimulationState, selector: ActionSelector): SimulationState {
        val base = super.afterAction(state, selector)
        return when (base.turn) {
            2 -> base
                .copy(scenarioStatus = OnsenStatus(state.support.map { it.card }, state.factor))

            24 -> base
                .addAllStatus(10, 150)
                .allTrainingLevelUp()

            48 -> base
                .addAllStatus(30, 200, mapOf("" to 1))
                .allTrainingLevelUp()

            72 -> base
                .addAllStatus(40, 300, mapOf("" to 3, "" to 3))
                .addOnsenTicket(max(1, onsenTicketOnDig[base.onsenStatus?.hoshinaRarity ?: 0]))

            73 -> base
                .addStatus(Status(skillHint = mapOf("" to 1)))

            else -> base
        }
    }

    private fun SimulationState.addOnsenTicket(count: Int): SimulationState {
        return updateOnsenStatus {
            copy(onsenTicket = min(3, onsenTicket + count))
        }
    }

    override fun afterSimulation(state: SimulationState): SimulationState {
        return state.addAllStatus(status = 50, skillPt = 250, skillHint = mapOf("" to 2))
    }
}
